<template>
  <div>
    <!-- header -->
    <div class="header">
      <van-nav-bar title="文章详情" left-arrow fixed placeholder @click-left="$router.back()" />
    </div>
    <!-- 加载特效 -->
    <div class="loading" v-if="Object.keys(artDetail).length === 0">
      <van-loading color="#0094ff">文章疯狂加载ing...</van-loading>
    </div>
    <!-- 文章详情 -->
    <div v-else>
      <div class="detail-box">
        <!-- 文章标题 -->
        <h1 class="art-title">{{ artDetail.title }}</h1>
        <!-- 作者信息 -->
        <van-cell :title="artDetail.aut_name" :label="artDetail.pubdate" center>
          <template>
            <van-button type="info" size="mini" v-if="artDetail.is_followed" @click="followFn(false)"
              >已关注</van-button
            >
            <van-button plain type="info" size="mini" icon="plus" v-else @click="followFn(true)">关注</van-button>
          </template>
          <template #icon>
            <img class="avatar" :src="artDetail.aut_photo" alt="" />
          </template>
        </van-cell>
        <!-- 分割线 -->
        <van-divider />
        <!-- 文章内容 -->
        <div class="art-content" v-html="artDetail.content"></div>
        <!-- 结束分割线 -->
        <van-divider>End</van-divider>
        <!-- 点赞按钮 -->
        <div class="thumb">
          <van-button type="danger" icon="good-job" size="small" v-if="artDetail.attitude === 1" @click="likeFn(0)"
            >已点赞</van-button
          >
          <van-button plain icon="good-job-o" type="danger" size="small" v-else @click="likeFn(1)">点赞</van-button>
        </div>
      </div>

      <!-- 评论列表组件 -->
      <div class="cmt-list" :class="isShowTextarea ? 'cmt-container-2' : 'cmt-container-1'">
        <CommentList ref="cmtListRef" />
      </div>
    </div>
    <!-- 底部评论发布及操作面板 -->
    <div>
      <!-- 操作面板 -->
      <div class="footer-first van-hairline--top" v-if="!isShowTextarea">
        <!-- 返回图标 -->
        <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
        <!-- 评论输入框(展示) -->
        <div class="cmt-input" @click="isShowTextarea = true">发表评论</div>
        <!-- 右侧图标区 -->
        <div class="icon-right">
          <van-badge :content="artDetail.comm_count ? artDetail.comm_count : ''">
            <van-icon name="comment-o" size="0.53333rem" @click="moveFn" />
          </van-badge>
          <van-icon
            name="star"
            color="#ee0a24"
            size="0.53333rem"
            v-if="artDetail.is_collected"
            @click="collectFn(false)"
          />
          <van-icon name="star-o" size="0.53333rem" v-else @click="collectFn(true)" />
          <van-icon name="share-o" size="0.53333rem" />
        </div>
      </div>
      <!-- 点击评论输入框后展示书写评论的文本域 -->
      <div class="footer-second van-hairline--top" v-else>
        <!-- 文本域 -->
        <textarea v-focus placeholder="友善评论、理性发言、阳光心灵" v-model="cmtContent" @blur="blurFn"></textarea>
        <!-- 发布按钮 -->
        <van-button type="default" :disabled="!cmtContent.trim()" @click="publish">发布</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import CommentList from './CommentList'
export default {
  name: 'ArticleDetail',
  components: {
    CommentList
  },
  data() {
    return {
      artDetail: {},
      isShowTextarea: false,
      cmtContent: '' // 收集用户输入的评论
    }
  },
  methods: {
    async getArtDetail() {
      const res = await this.$API.reqArtDetail(this.$route.query.aid)
      this.artDetail = res.data.data
    },
    // 关注或取关作者
    followFn(flag) {
      // 切换按钮
      this.artDetail.is_followed = flag
      // 通知后台
      if (flag) {
        // 关注
        this.$API.reqFollow(this.artDetail.aut_id)
      } else {
        // 取关
        this.$API.reqCancelFollow(this.artDetail.aut_id)
      }
    },
    // 点赞与取消点赞
    likeFn(flag) {
      // 切换按钮
      this.artDetail.attitude = flag
      // 通知后台
      if (flag) {
        // 点赞
        this.$API.reqLike(this.artDetail.art_id)
      } else {
        // 取消点赞
        this.$API.reqCancelLike(this.artDetail.art_id)
      }
    },
    blurFn() {
      // 因为使用v-if,失去焦点先执行就销毁了, 没法点击发布按钮
      // 所以要延迟blur事件
      setTimeout(() => {
        this.isShowTextarea = false
      })
    },
    // 发表评论
    async publish() {
      const res = await this.$API.reqPublishCmt({
        id: this.$route.query.aid,
        content: this.cmtContent.trim()
      })
      // 将返回的新评论对象放进评论列表组件的cmtList中
      this.$refs.cmtListRef.cmtList.unshift(res.data.data.new_obj)
      // 页面移动到第一条评论
      this.moveFn()
      // 徽标数字加1
      this.artDetail.comm_count++
      // 清除数据
      this.cmtContent = ''
    },
    // 点击底部导航中的评论图标,页面移动到评论列表
    moveFn() {
      // 让点赞容器滚动到页面最上面
      document.querySelector('.thumb').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 文章收藏或取消收藏
    collectFn(flag) {
      // 切换图标
      this.artDetail.is_collected = flag
      if (flag) {
        // 收藏
        this.$API.reqCollect(this.$route.query.aid)
      } else {
        // 取消收藏
        this.$API.reqCancelCollect(this.$route.query.aid)
      }
    }
  },
  created() {
    this.getArtDetail()
  }
}
</script>

<style lang="less" scoped>
.detail-box {
  padding: 10px;
}
.art-title {
  margin: 10px 0;
  font-size: 16px;
}
.avatar {
  margin-right: 5px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 0;
  background-color: #f8f8f8;
}
.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
  /deep/ img {
    width: 100%;
  }
  /deep/ pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
.van-cell {
  padding: 5px 0;
  &::after {
    border: 0;
  }
}
.thumb,
.loading {
  display: flex;
  justify-content: center;
}
.footer-first {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 45px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: #fff;

  .cmt-input {
    flex: 1;
    height: 32px;
    padding-left: 15px;
    margin-left: 15px;
    border-radius: 16px;
    background-color: #f8f8f8;
    font-size: 12px;
    line-height: 32px;
  }
  .icon-right {
    display: flex;
    justify-content: space-evenly;
    width: 146px;
    line-height: 0;
  }
}
.footer-second {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 0;
  height: 80px;
  width: 100%;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: #fff;
  textarea {
    flex: 1;
    width: 100%;
    height: 66%;
    padding: 5px;
    background-color: #f8f8f8;
    border: 1px solid #efefef;
    border-radius: 5px;
    resize: none;
    font-size: 12px;
  }

  .van-button {
    height: 100%;
    border: 0;
  }
}
.cmt-container-1 {
  padding-bottom: 46px;
}
.cmt-container-2 {
  padding-bottom: 80px;
}
</style>
