<template>
  <div>
    <van-list
      class="cmt-container"
      v-model="loading"
      :finished="finished"
      finished-text="人家是有底线的哦"
      @load="onLoad"
    >
      <div class="cmt-item" v-for="cmt in cmtList" :key="cmt.com_id">
        <div class="cmt-header">
          <div class="user">
            <img class="avatar" :src="cmt.aut_photo" alt="" />
            <span>{{ cmt.aut_name }}</span>
          </div>
          <div class="like-icon">
            <van-icon
              name="like"
              color="#ff0000"
              size="0.42667rem"
              v-if="cmt.is_liking"
              @click="cmtLikeFn(cmt, false)"
            />
            <van-icon name="like-o" color="#838787" size="0.42667rem" v-else @click="cmtLikeFn(cmt, true)" />
          </div>
        </div>
        <div class="cmt-body">{{ cmt.content }}</div>
        <div class="cmt-footer">{{ formatTime(cmt.pubdate) }}</div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { timeAgo } from '@/utils/date'
export default {
  name: 'CommentList',
  data() {
    return {
      loading: false,
      finished: false,
      offset: null, // 作为下一页的offset值, 值为评论id
      cmtList: []
    }
  },
  methods: {
    async getCmtList() {
      const obj = {
        id: this.$route.query.aid,
        offset: this.offset
      }
      const res = await this.$API.reqCmtList(obj)
      // 关闭加载特效
      this.loading = false
      const { last_id, results } = res.data.data
      // 判断数据是否加载完
      if (!last_id) return (this.finished = true)
      this.offset = last_id
      this.cmtList = [...this.cmtList, ...results]
    },
    onLoad() {
      this.getCmtList()
    },
    formatTime: timeAgo,
    // 评论点赞或取消点赞
    cmtLikeFn(cmt, flag) {
      // 切换按钮
      cmt.is_liking = flag
      if (flag) {
        // 点赞
        this.$API.reqLikeCmt(cmt.com_id)
      } else {
        // 取消点赞
        this.$API.reqCancelLikeCmt(cmt.com_id)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cmt-container {
  padding: 10px 20px 0;
}
.cmt-item {
  padding: 15px 0;
  font-size: 12px;
  border-bottom: 1px solid #f8f8f8;
  .cmt-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .cmt-body {
    margin-top: 6px;
    text-indent: 2em;
    font-size: 14px;
  }
  .cmt-footer {
    margin-top: 10px;
    color: #808080;
  }
}

.avatar {
  width: 40px;
  height: 40px;
  margin-right: 15px;
  border-radius: 50%;
  background-color: #f8f8f8;
  vertical-align: middle;
}
</style>
